.rmd-btn__with-wave,
.rmd-btn__with-wave-o {
  overflow: hidden;
  position: relative;
  z-index: 0; }

.rmd-btn--wave {
  pointer-events: none;
  z-index: -1;
  position: absolute;
  visibility: hidden;
  top: -1000px;
  left: -1000px; }



